export type icon_name = "activity" | "atom" | "bell" | "book" | "calendar" |
    "certificate" | "check_square" | "chevron_right" | "chevrons_right" | "circle" | "clock" | "close" |
    "disc" | "edit" | "graduation_cap" | "heart" | "home" | "logout" | "mail" | "menu" | "message" |
    "moon" | "more_horizontal" | "more_vertical" | "pie" | "save" |
    "search" | "settings" | "staff" | "star" | "student" | "sun" | "teacher" | "trash"

export function SVG(name: icon_name, classes: string): string {
    return SVGS[name].replace("{classNames}", classes)
}
